<template>
  <div class="guide">
    <img :src="isCloth ? require('@/assets/img/guide/workspace2.png')
      : require('@/assets/img/guide/workspace.png')" @load="endLoad">
    <template v-if="endLoadFlag">
      <highlight-area
        left="430"
        top="259"
        width="220"
        height="240"
        :opacity="opacity">
        <div @contextmenu.prevent="$refs.ctxMenu.open" style="height: 100%; width: 100%"></div>
        <ContextMenu ref="ctxMenu">
          <li class="ctx-menu-item" @click="showShoe"><i class="iconfont icon-paste"></i>
          {{ $t('file-list.commands.paste') }}
          </li>
        </ContextMenu>
      </highlight-area>
      <!-- eslint-disable max-len -->
      <guide-board
        :guide-image="require('@/assets/img/guide/shoe-paste.png')"
        :title="$t('guide.paste_title')"
        :content="$t('guide.paste_content')"
        current-step="3"
        total-step="7"
        top="341"
        left="680"
        link-direction="left"></guide-board>
    </template>
  </div>
</template>

<script>
import ContextMenu from '@/components/context-menu/ContextMenu';
import mixin from '../loadImgMixin';

export default {
  components: {
    ContextMenu,
  },
  mixins: [mixin],
  props: ['isCloth'],
  data() {
    return {
      opacity: false,
    };
  },
  methods: {
    showShoe() {
      this.opacity = true;
      setTimeout(() => {
        this.nextStep();
      }, 1000);
    },
    nextStep() {
      this.$emit('nextStep');
    },
  },
};
</script>

