<template>
  <div
    @contextmenu.prevent.stop
    :style="{ width: clientWidth+'px',height: clientWidth*0.5625 + 'px' }">
    <!-- <transition name="fade" mode="out-in">

      <component :is="'step'+currentStep" @nextStep="nextStep" />
    </transition> -->
    <step1 :isCloth="isCloth" v-show="currentStep === 1" @nextStep="nextStep" />
    <step2 :isCloth="isCloth" v-show="currentStep === 2" @nextStep="nextStep" />
    <step3 :isCloth="isCloth" v-show="currentStep === 3" @nextStep="nextStep" />
    <step4 :isCloth="isCloth" v-show="currentStep === 4" @nextStep="nextStep" />
    <step5 :isCloth="isCloth" v-show="currentStep === 5" @nextStep="nextStep" />
    <step6 :isCloth="isCloth" v-show="currentStep === 6" @nextStep="nextStep" />
    <step7 :isCloth="isCloth" v-show="currentStep === 7" @nextStep="nextStep" />
    <step8 :isCloth="isCloth" v-show="currentStep === 8" @nextStep="nextStep" />
    <step9 :isCloth="isCloth" v-show="currentStep === 9" @nextStep="nextStep" />
  </div>
</template>

<script>
import Vue from 'vue';
import { getContextUser } from '@/loaders/context';

import step1 from './step1';
import step2 from './step2';
import step3 from './step3';
import step4 from './step4';
import step5 from './step5';
import step6 from './step6';
import step7 from './step7';
import step8 from './step8';
import step9 from './step9';

export default {
  props: {
    step: String,
  },
  components: {
    step1, step2, step3, step4, step5, step6, step7, step8, step9,
  },
  provide() {
    return {
      vm: this.vm,
    };
  },
  data() {
    return {
      currentStep: 1,
      vm: new Vue(), // 示例化一个vue，做该部件的事件传递的中间者
    };
  },
  mounted() {
    this.vm.$on('quitTutorial', this.quitTutorial);
    this.vm.$on('nextStep', this.nextStep);
  },
  destroyed() {
    this.vm = null;
  },
  methods: {
    // 退出教程
    quitTutorial() {
      this.currentStep = 9;
    },
    // 下一步
    nextStep() {
      this.currentStep += 1;
    },
    // 上一步，用于调试
    previousStep() {
      if (this.currentStep > 1) {
        this.currentStep -= 1;
      }
    },
  },
  computed: {
    // 屏幕可用分辨率
    clientWidth() {
      return window.screen.availWidth < 1920 ? 1920 : window.screen.availWidth;
    },
    /** @return { boolean } */
    isCloth() {
      const user = getContextUser();
      return user && user.businessType === 2;
    },
  },
};
</script>
