<script>
import mixin from '../loadImgMixin';

export default {
  mixins: [mixin],
  props: ['isCloth'],
  data() {
    return {
      radio: '1',
      guideContent: '',
      showBtn: false,
      isLastStep: false,
      showRemarks: false,
    };
  },
  methods: {
    nextStep() {
      this.isLastStep = false;
      this.$emit('nextStep');
    },
    // 点击跳过
    skip() {
      this.showBtn = true;
    },
    // 显示remark和确定按钮
    showRemarksComfireButton() {
      this.showRemarks = true;
      setTimeout(() => {
        this.showBtn = true;
      }, 500);
    },
    // 显示确定按钮
    showComfireButton() {
      setTimeout(() => {
        this.showBtn = true;
      }, 500);
    },

  },
};
</script>

