<script>
import i18n from 'i18next';
import mixin from '../loadImgMixin';

const content1 = i18n.t('guide.invite_guest_2_content1');
const content2 = i18n.t('guide.invite_guest_2_content2');
export default {
  mixins: [mixin],
  props: ['isCloth'],
  data() {
    return {
      guideBoardTop: '180',
      guideBoardLeft: '1340',
      guideBoardContent: content1,
    };
  },
  methods: {
    nextStep() {
      this.$emit('nextStep');
    },
    changePosition(bool) {
      if (!bool) {
        this.guideBoardTop = '180';
        this.guideBoardLeft = '1340';
        this.guideBoardContent = content1;
      } else {
        this.guideBoardTop = '270';
        this.guideBoardLeft = '1160';
        this.guideBoardContent = content2;
      }
    },
  },
};
</script>

